.sobre-festival {
    padding: $separacion 0;

    @include tablet {
       display: flex;
       grid-template-columns: 1fr 1fr;
       column-gap: 5rem;
    }
}
.contenido-festival {
    display: flex;
    flex-direction: column;
    justify-content: center;

    h2 {
        font-size: 3rem;
    }
    .fecha {
        color: $rosa;
        font-weight: $bold;
    }
    .texto {
        line-height: 1.5;
    }
}
       
 
